@import "variables/_variables";

html, .maintenance-wrapperdy {
  position: relative;
  height: 100%;
}
.maintenance-wrapper {
  background-color: $victoria;
  color: #fff;
  font-family: Arial,sans-serif;
  margin: 0;
  font-size: 16px;
  height: 100%;
}

.text-container {
  position: relative;
  overflow: auto;
}

.text-body {
  padding: 0 1em;
  overflow: auto;
  position: relative;
  margin: 0 auto;
  @media screen and (min-width: 768px) {
    width: 500px;
  }
}

.branding {
  padding: 1em 0;
}

.brand-logo {
  display: block;
  margin: 0 auto;
  max-width: 200px;
}

.brand-icon {
  display: block;
  margin: 2em auto;
  max-width: 60px;
}

.footer {
  background-color: #fff;
  color: #7b8c8f;
  padding: 20px 0;
  text-align: center;
  font-size: 0.813em;
  position: absolute;
  width: 100%;
  bottom: 0;
}
